.your-score-wrapper {
    background-color: #C4C4C4;
    border-radius: 10px;
    padding: 10px 0 20px 0;
    width: 300px;
    margin: 30px auto;
}

.your-score-wrapper h1 {
    color: #333F48;
    font-size: 1.5rem;
}

.your-score-wrapper p {
    color: #333F48;
    font-size: 1.2rem;
}
